import {  Pagination } from '@mui/material';
import React from 'react'




const CustomPagination = ({setPage,numOfPages = 10}) => {
    const nextPageChange = (page)=>{
        setPage(page);
        window.scrollTo(0,0)
    }
  return (
    <div  style={{width : "100%",
    display : "flex",
    justifyContent : "center",
    marginTop : 10,
    paddingBottom : "5rem"}}>

        <Pagination  
         count={numOfPages} onChange={(e)=> nextPageChange(e.target.textContent)} style={{textDecorationColor:"white"}} color="primary"/>
    </div>
  )
}

export default CustomPagination;